:root {
    --color-1: #fff;
    --color-3: #E89025;
}

header, main {
    max-width: 80%;
}

main {
    p {
        max-width: 35em;
        margin-left: auto;
        margin-right: auto;
    }
}

header {
    margin: 2em auto 0;
    text-align: center;

    p {
        text-transform: uppercase;
        letter-spacing: 0.2em;

        color: var(--color-3);
    }

    img {
        max-width: 45em;
    }
    
    animation: fadeIn;
    animation-duration: 1s;
}

h1 {
    font-size: 4em;
    margin-top: 2rem;
    line-height: 1;
}

section + section {
    margin-top: 2em;
}

#info {
    font-style: italic;
    p + p {
        margin-top: 0;
    }
}

#palette img {
    margin: 2em auto;
    max-width: 50%;
}